<template>
  <div class="dataAnalysis tapStyle">
    <div class="toolbox">
      <div class="toolbox-row">
        <span>时间选择 : </span>
        <div class="layoutBox" style="width:242px">
              <el-date-picker v-model="dateRange" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
        </div>&emsp;
        <el-button @click="getTableData"><i class="iconfont icon-sousuo"></i>查询</el-button>
        <el-button @click="exportExcel" type='delet'><i class="iconfont icon-daochu"></i>导出</el-button>
      </div>
    </div>
    <div class="containerbox" style="height: calc(100% - 6px);padding: 6px 0;">
      <div class="table-wrap" style="height: calc(100% - 35px);">
        <el-table
          :data="tableData"
          height="100%"
          ref="table"
          border
          style="width: 100%">
          <el-table-column
            type="index"
            label="序号">
          </el-table-column>
          <el-table-column
            prop="stationProvin"
            label="行政区">
          </el-table-column>
          <el-table-column
            prop="riverBasin"
            label="流域">
          </el-table-column>
          <el-table-column
            prop="stationName"
            label="断面名称">
          </el-table-column>
          <el-table-column
            prop="projName"
            label="监测项目">
          </el-table-column>
          <el-table-column
            prop="yUploadNum"
            label="应上传数量">
          </el-table-column>
          <el-table-column
            prop="sUploadNum"
            label="实际上传数量">
          </el-table-column>
          <el-table-column
            prop="uploadRate"
            label="传输率">
          </el-table-column>
          <el-table-column
            prop="validNum"
            label="有效数据数量">
          </el-table-column>
          <el-table-column
            prop="validRate"
            label="数据有效率">
          </el-table-column>
        </el-table>
      </div>
      <div class="paginationBox">
        <el-pagination
          @size-change="paginationSizeChange"
          @current-change="paginationChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          background
          :small = "true"
          popper-class = "select-common-option"
          :page-sizes = "pageList"
          layout="total,sizes,slot,->,prev, pager, next, jumper"
          :total="totalRecords">
          <span class="el-pagination__current">当前第{{currentPage}}/{{totalPage}}页</span>
        </el-pagination>
      </div>
    </div>

  </div>
</template>

<script>
  import {exportTableData} from '../../assets/js/tableExport'
  export default {
    name: "dataAnalysis",
    data() {
      return {
        dateRange: "", //时间选择
        tableData: [],
        stationId:'',
        currentPage: 1, //分页组件当前页
        pageSize: 10, //分页每页多少条数据
        totalRecords: 0, //总条数
        pageList: [10, 15, 20], //设置每页多少条
      };
    },
    computed: {
      totalPage() {
        //计算总页数
        let number = 0;
        number = Math.ceil(this.totalRecords / this.pageSize);
        console.log();
        if(isNaN(number)){
          return 1;
        }else{
          return number === 0 || number === 'NaN' ? 1 : number;
        }

      },
      stationInfo: {
        get: function() {
          return this.$store.state.stationInfo;
        }
      }
    },
    methods:{
      paginationSizeChange(val){
        //分页pageSize改变时执行
        this.pageSize = val;
        this.getTableData();
      },
      paginationChange(val){
        //分页页码改变时执行
        this.currentPage = val;
        this.getTableData();
      },
      //获取表格数据
      getTableData(){
        this.$http.get('/Quality/qualityAnalysis/effectivenessData',{
          params:{
            startTime: this.dateRange[0].format('yyyy-MM-dd'),
            endTime: this.dateRange[1].format('yyyy-MM-dd'),
            stationCodes: this.stationId,
            pageNumber: this.currentPage,
            pageSize: this.pageSize
          }
        }).then(res =>{
          if(res.data.code === 0){
            this.tableData = res.data.content.dataList;
            this.totalRecords = res.data.content.totalRecords;
          }
        }).catch(error =>{
        })
      },
      //导出表格
      exportExcel(){
        let {table} = this.$refs;
        let tableBox = table.$el;
        let header = tableBox.querySelector('.el-table__header');
        let title = '数据有效性分析';
        let params = {
          headerProp:'stationProvin,riverBasin,stationName,projName,yUploadNum,sUploadNum,uploadRate,validNum,validRate',
          startTime: this.dateRange[0].format('yyyy-MM-dd'),
          endTime: this.dateRange[1].format('yyyy-MM-dd'),
          stationCodes: this.stationId,
          tableType:'DATA_VALIDITY',
          // ifCurrentSection:false,
          pattern:'yyyy-MM-dd'
        };
        exportTableData(header,title,'','1',"/Quality/exports/qualityControlExcel",params);
      },
    },
    created() {
      this.$store.commit("setShowlefttree", true);
      this.dateRange = [new Date(new Date().setDate(new Date().getDate()-7)),new Date()];
    },
    watch: {
      stationInfo(value) {
        if (value.length > 0) {
          let list = [];
          value.forEach((item)=>{
            list.push(item.nodeId);
          });
          this.stationId = list.join(',');
          this.getTableData();
        }
      }
  }
};
</script>

<style scoped lang="less">
.dataAnalysis {
  height: calc(100% - 30px);
  .containerbox {
    padding: 6px 12px;
    height: calc(100% - 68px);
  }
}
.editInput {
  width: 50px;
  border: none;
  outline: none;
}
.toolbox-row {
  margin-bottom: 6px;
  &:last-child {
    margin-bottom: 0;
  }
}
</style>



